function getAvatarUrl(char, size = "36x36", bgColor = "82a9ff", textColor = "0d0f1a", font = "orbitron") {
    const isEmoji = /\p{Emoji}/u.test(char);
    if (isEmoji) {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        const dpr = window.devicePixelRatio || 1;
        const sizePx = parseInt(size.split('x')[0]) * dpr;
        canvas.width = sizePx;
        canvas.height = sizePx;
        ctx.fillStyle = `#${bgColor}`;
        ctx.fillRect(0, 0, sizePx, sizePx);
        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';
        ctx.font = `${sizePx * 0.65}px sans-serif`;
        ctx.fillText(char, sizePx / 2, sizePx / 2 + sizePx * 0.08);
        return canvas.toDataURL();
    } else {
        return `https://placehold.co/${size}/${bgColor}/${textColor}?text=${encodeURIComponent(char.toUpperCase())}&font=${font}`;
    }
}

