function showToast(message, type = 'info', duration = 3500) {
    if (toastTimeout) clearTimeout(toastTimeout);
    let iconClass = 'fa-info-circle';
    if (type === 'success') iconClass = 'fa-check-circle';
    else if (type === 'error') iconClass = 'fa-times-circle';
    else if (type === 'warning') iconClass = 'fa-exclamation-triangle';

    toastNotification.innerHTML = `<i class="fas ${iconClass} mr-3 text-xl"></i> <div class="flex-1">${message}</div>`;
    toastNotification.className = 'fixed bottom-6 right-6 z-[200] transition-all duration-300 px-5 py-4 rounded-xl shadow-xl text-sm font-medium flex items-start min-w-[300px] max-w-md';

    toastNotification.classList.remove('opacity-0', 'translate-y-5', 'scale-95', 'success', 'error', 'warning', 'info');

    if (type === 'success') {
        toastNotification.style.backgroundColor = 'var(--success-color)';
        toastNotification.style.color = 'white';
    } else if (type === 'error') {
        toastNotification.style.backgroundColor = 'var(--error-color)';
        toastNotification.style.color = 'white';
    } else if (type === 'warning') {
        toastNotification.style.backgroundColor = 'var(--warning-color)';
        toastNotification.style.color = document.documentElement.classList.contains('light') ? '#573a00' : '#fef3c7';
    } else {
        toastNotification.style.backgroundColor = 'var(--text-accent)';
        toastNotification.style.color = 'white';
    }

    requestAnimationFrame(() => {
        toastNotification.style.opacity = '1';
        toastNotification.style.transform = 'translateY(0) scale(1)';
    });
    toastTimeout = setTimeout(() => {
        toastNotification.style.opacity = '0';
        toastNotification.style.transform = 'translateY(5px) scale(0.95)';
    }, duration);
}

function navigateTo(targetPageId) {
    document.querySelectorAll('.page').forEach(page => {
        if (page.id === targetPageId) {
            if (page.classList.contains('hidden')) {
                page.classList.remove('hidden');
                page.classList.add('entering');
                requestAnimationFrame(() => requestAnimationFrame(() => page.classList.remove('entering')));
            }
        } else if (!page.classList.contains('hidden')) page.classList.add('hidden');
    });
    if (targetPageId !== 'chatPage' && isEmojiPickerOpen) toggleEmojiPicker(false);
    if (targetPageId === 'settingsPage') loadSettingsToUI();
}

function applyTheme(isLight) {
    const htmlEl = document.documentElement;
    if (isLight) {
        htmlEl.classList.add('light');
        themeToggleIcon.classList.remove('fa-moon');
        themeToggleIcon.classList.add('fa-sun');
        if (darkModeToggle) darkModeToggle.checked = true;
    } else {
        htmlEl.classList.remove('light');
        themeToggleIcon.classList.remove('fa-sun');
        themeToggleIcon.classList.add('fa-moon');
        if (darkModeToggle) darkModeToggle.checked = false;
    }
    userAvatarHeader.src = getAvatarUrl(currentUserAvatarChar, "36x36", isLight ? "3b82f6" : "82a9ff", isLight ? "ffffff" : "0d0f1a");
    renderUserList();
    setTimeout(() => Prism.highlightAll(), 50);
}

function toggleTheme() {
    const isLight = document.documentElement.classList.toggle('light');
    applyTheme(isLight);
    localStorage.setItem(STORAGE_KEY_THEME, isLight ? 'light' : 'dark');
}

function loadConfig() {
    const savedConfig = localStorage.getItem(STORAGE_KEY_CONFIG);
    if (savedConfig) {
        try {
            const parsedConfig = JSON.parse(savedConfig);
            apiConfig = { ...apiConfig,
                ...parsedConfig
            };
            if (!Array.isArray(apiConfig.models)) apiConfig.models = [];
            if (!Array.isArray(apiConfig.customBodyParams)) apiConfig.customBodyParams = [];
        } catch (e) {
            console.error('Failed to parse saved API config:', e);
            showToast('æ— æ³•åŠ è½½APIé…ç½®ï¼Œä½¿ç”¨é»˜è®¤è®¾ç½®ã€‚', 'error');
            localStorage.removeItem(STORAGE_KEY_CONFIG);
        }
    }

    if (apiConfig.provider !== 'custom') {
        updateApiUrlForProvider(apiConfig.provider, false);
    } else if (apiConfig.provider === 'custom' && !apiConfig.apiUrl) {
        apiConfig.apiUrl = '';
    }

    const savedUsername = settingsUsernameInput.value.trim();
    if (savedUsername) {
        currentUsername = savedUsername;
        currentUserAvatarChar = currentUsername.charAt(0)?.toUpperCase() || 'O';
        usernameHeader.textContent = currentUsername;
    }

    updateModelSelectDropdown();
    renderCustomBodyParamsList();
}

function updateApiUrlForProvider(provider, shouldSaveConfig = true) {
    switch (provider) {
        case 'deepseek':
            apiConfig.apiUrl = 'https://api.deepseek.com/v1';
            break;
        case 'openai':
            apiConfig.apiUrl = 'https://api.openai.com/v1';
            break;
        case 'gemini':
            apiConfig.apiUrl = 'https://generativelanguage.googleapis.com/v1beta/models/';
            break;
        case 'custom':
            break;
        default:
            apiConfig.apiUrl = 'https://generativelanguage.googleapis.com/v1beta/models/';
    }
    if (settingsApiUrlInput && provider !== 'custom') {
        settingsApiUrlInput.value = apiConfig.apiUrl;
    }
    if (shouldSaveConfig) saveConfig();
}

function saveConfig() {
    try {
        localStorage.setItem(STORAGE_KEY_CONFIG, JSON.stringify(apiConfig));
        return true;
    } catch (e) {
        console.error('Failed to save API config:', e);
        showToast('ä¿å­˜APIé…ç½®å¤±è´¥ï¼', 'error');
        return false;
    }
}

