function configureMarkdown() {
    marked.setOptions({
        breaks: false,
        gfm: true,
        highlight: (code, lang) => {
            const language = lang || 'plaintext';
            if (Prism.languages[language]) {
                return Prism.highlight(code, Prism.languages[language], language);
            }
            return code;
        }
    });
    const renderer = new marked.Renderer();
    renderer.code = function(code, infostring, escaped) {
        const lang = (infostring || '').match(/\S*/)[0];
        const highlightedCode = this.options.highlight(code, lang);
        
        let langClass = lang ? `${this.options.langPrefix}${lang}` : `${this.options.langPrefix}plaintext`;
        
        return `<pre class="${langClass}" data-lang="${lang || 'plaintext'}"><code class="${langClass}">${highlightedCode}\n</code></pre>\n`;
    };

    const originalParagraph = renderer.paragraph.bind(renderer);
    renderer.paragraph = (text) => {
        text = text.replace(/\$(.+?)\$/g, (match, formula) => {
            try {
                return katex.renderToString(formula, { throwOnError: false, displayMode: false });
            } catch (e) {
                return `<span class="katex-error" title="${e.message}">${match}</span>`;
            }
        });
        text = text.replace(/\$\$([\s\S]+?)\$\$/g, (match, formula) => {
            try {
                return katex.renderToString(formula.trim(), { throwOnError: false, displayMode: true });
            } catch (e) {
                return `<div class="katex-error" title="${e.message}"><pre>${formula.trim()}</pre></div>`;
            }
        });
        return originalParagraph(text);
    };
    marked.use({
        renderer
    });
}


function highlightCodeInElement(element) {
    if (typeof Prism !== 'undefined') {
        Prism.highlightAllUnder(element || document.body);
        addOfficeActionButtons(element || document.body);
    }
}

function renderMessageContent(rawContent) {
    let htmlContent = marked.parse(rawContent);
    // For a private tool, we can trust the content from the API.
    // However, it's still good practice to sanitize, especially if user-quoted content is involved.
    // We'll keep DOMPurify for this reason.
    return DOMPurify.sanitize(htmlContent, {
        USE_PROFILES: {
            html: true
        },
        ADD_TAGS: ['math', 'annotation', 'semantics', 'mtext', 'mn', 'mo', 'mi', 'mspace', 'mover', 'munder', 'munderover', 'mstyle', 'mfrac', 'msqrt', 'mroot', 'mtable', 'mtr', 'mtd', 'mlabeledtr'],
        ADD_ATTR: ['encoding', 'xmlns', 'class', 'style', 'aria-hidden', 'title', 'start', 'data-lang']
    });
}

function displayMessage(sender, text, isOwnMessage, avatarChar = '?', timestamp = null, messageId = null, reasoningContent = null) {
    if (!text && !isOwnMessage && !reasoningContent && !messageId) return;
    const msgId = messageId || `msg-${Date.now()}-${Math.random().toString(16).slice(2)}`;
    let existingMessageElement = document.getElementById(msgId);

    if (existingMessageElement && !isOwnMessage) {
        const bubble = existingMessageElement.querySelector('.message-bubble');
        const textElement = bubble.querySelector('.message-text');
        if (textElement) {
            textElement.innerHTML = renderMessageContent(text || '');
            highlightCodeInElement(textElement);
        }
        if (apiConfig.provider === 'deepseek' && apiConfig.reasoning && reasoningContent !== null) {
            let reasoningElement = bubble.querySelector('.reasoning-content');
            if (!reasoningElement && reasoningContent) {
                reasoningElement = document.createElement('div');
                reasoningElement.className = 'reasoning-content';
                bubble.insertBefore(reasoningElement, textElement);
            }
            if (reasoningElement) reasoningElement.textContent = reasoningContent;
        }
        lastMessageElement = existingMessageElement;
        scrollToBottom();
        return existingMessageElement;
    }

    const messageElement = document.createElement('div');
    messageElement.id = msgId;
    messageElement.classList.add('flex', 'items-start', 'max-w-full', 'group', 'mb-1.5', 'py-2');

    const avatarColorIsLight = document.documentElement.classList.contains('light');
    const avatarBg = isOwnMessage ? (avatarColorIsLight ? "3b82f6" : "6a82fb") : (sender === AI_ASSISTANT_NAME ? (avatarColorIsLight ? "10b981" : "2dd4bf") : "a0a8c0");
    const avatarText = isOwnMessage ? (avatarColorIsLight ? "ffffff" : "e0e7ff") : (sender === AI_ASSISTANT_NAME ? (avatarColorIsLight ? "ffffff" : "0d0f1a") : "1a1c2d");

    const avatarElement = document.createElement('img');
    avatarElement.src = getAvatarUrl(avatarChar, "36x36", avatarBg, avatarText);
    avatarElement.alt = `${sender} avatar`;
    avatarElement.classList.add('w-9', 'h-9', 'rounded-full', 'flex-shrink-0', 'shadow-md', 'border-2', 'border-[var(--bg-secondary)]');

    const messageContentWrapper = document.createElement('div');
    messageContentWrapper.className = 'flex flex-col mx-2.5 flex-grow min-w-0';

    const bubble = document.createElement('div');
    bubble.classList.add('message-bubble', 'relative');
    bubble.classList.add(isOwnMessage ? 'my-message' : 'other-message');

    const textElement = document.createElement('div');
    textElement.classList.add('message-text', 'break-words');
    textElement.innerHTML = (sender === AI_ASSISTANT_NAME && !text) ? '' : renderMessageContent(text || '');

    bubble.appendChild(textElement);

    if (apiConfig.provider === 'deepseek' && apiConfig.reasoning && reasoningContent && !isOwnMessage) {
        const reasoningElement = document.createElement('div');
        reasoningElement.className = 'reasoning-content';
        reasoningElement.textContent = reasoningContent;
        bubble.appendChild(reasoningElement);
    }
    
    messageContentWrapper.appendChild(bubble);

    if (!isOwnMessage && text) {
        const actionsContainer = document.createElement('div');
        actionsContainer.className = 'message-actions mt-1.5';
        
        const insertBtn = document.createElement('button');
        insertBtn.className = 'message-action-btn';
        insertBtn.innerHTML = '<i class="fas fa-file-download mr-1"></i> æ’å…¥å…¨æ–‡';
        insertBtn.onclick = () => insertContent(textElement.innerText);
        actionsContainer.appendChild(insertBtn);
        
        messageContentWrapper.appendChild(actionsContainer);
    }

    if (isOwnMessage) {
        messageElement.classList.add('justify-end');
        messageContentWrapper.classList.add('items-end');
        messageElement.appendChild(messageContentWrapper);
        messageElement.appendChild(avatarElement);
    } else {
        messageElement.classList.add('justify-start');
        messageContentWrapper.classList.add('items-start');
        messageElement.appendChild(avatarElement);
        messageElement.appendChild(messageContentWrapper);
    }

    messageArea.appendChild(messageElement);
    while (messageArea.children.length > MAX_MESSAGES_DISPLAY + 1) {
        if (messageArea.firstElementChild?.querySelector('.text-xs.px-2')) {
            if (messageArea.children.length > 2) messageArea.removeChild(messageArea.children[1]);
            else break;
        } else {
            messageArea.removeChild(messageArea.firstElementChild);
        }
    }
    if (text) highlightCodeInElement(bubble);

    if (text || isOwnMessage) scrollToBottom();
    if (!isOwnMessage) lastMessageElement = messageElement;
    return messageElement;
}


function sendMessage() {
    const messageText = messageInput.value.trim();
    if (!messageText) return;
    if (abortController) {
        showToast('AI æ­£åœ¨å›žå¤ï¼Œè¯·ç¨å€™...', 'warning');
        return;
    }

    let systemPrompt = "è¯·åœ¨å›žå¤æ—¶éµå¾ªä»¥ä¸‹è§„åˆ™ï¼šå¦‚æžœä½ éœ€è¦æä¾›Excelå…¬å¼ï¼Œå¿…é¡»ä½¿ç”¨Markdownçš„ä»£ç æ¡†æ ¼å¼ï¼Œå¹¶æ ‡è®°è¯­è¨€ä¸º `formula`ï¼Œä¾‹å¦‚ï¼š```formula\n=SUM(A1:A10)\n```ã€‚å¯¹äºŽå…¶ä»–ä»£ç ï¼Œä¹Ÿè¯·ä½¿ç”¨å¯¹åº”çš„è¯­è¨€æ ‡è®°ã€‚";
    if (apiConfig.aiStyle !== 'default') {
        switch (apiConfig.aiStyle) {
            case 'professional': systemPrompt += ' è¯·ç”¨ä¸“ä¸šã€ä¸¥è°¨çš„è¯­æ°”å›žç­”ã€‚'; break;
            case 'friendly': systemPrompt += ' è¯·ç”¨å‹å¥½ã€äº²åˆ‡çš„è¯­æ°”å›žç­”ã€‚'; break;
            case 'humorous': systemPrompt += ' è¯·ç”¨å¹½é»˜ã€é£Žè¶£çš„è¯­æ°”å›žç­”ã€‚'; break;
            case 'concise': systemPrompt += ' è¯·ç”¨ç®€æ´ã€ç›´æŽ¥çš„æ–¹å¼å›žç­”ã€‚'; break;
            case 'detailed': systemPrompt += ' è¯·æä¾›è¯¦ç»†ã€å…¨é¢çš„å›žç­”ã€‚'; break;
        }
        if (apiConfig.aiStyleStrength > 1.1) systemPrompt += ` (é£Žæ ¼å¼ºåº¦: ${apiConfig.aiStyleStrength}, è¯·æ›´å¼ºè°ƒæ­¤é£Žæ ¼)`;
        else if (apiConfig.aiStyleStrength < 0.9) systemPrompt += ` (é£Žæ ¼å¼ºåº¦: ${apiConfig.aiStyleStrength}, è¯·æŸ”å’Œè¡¨è¾¾æ­¤é£Žæ ¼)`;
    }

    let messagesToSend = [];
    if (apiConfig.provider === 'openai' || apiConfig.provider === 'deepseek' || apiConfig.provider === 'custom') {
        if (systemPrompt) messagesToSend.push({ role: 'system', content: systemPrompt });
        messagesToSend = [...messagesToSend, ...currentConversationHistory.slice(-MAX_HISTORY_SENT_TO_API).map(m => ({ role: m.role, content: m.content }))];
        messagesToSend.push({ role: 'user', content: messageText });
    } else if (apiConfig.provider === 'gemini') {
        let currentTurnContent = messageText;
        currentConversationHistory.slice(-MAX_HISTORY_SENT_TO_API).forEach(msg => {
            messagesToSend.push({ role: msg.role === 'assistant' ? 'model' : 'user', parts: [{ text: msg.content }] });
        });
        if (systemPrompt && messagesToSend.length === 0) {
            currentTurnContent = systemPrompt + "\n\n" + messageText;
        }
        messagesToSend.push({ role: 'user', parts: [{ text: currentTurnContent }] });
    }

    displayMessage(currentUsername, messageText, true, currentUserAvatarChar);
    currentConversationHistory.push({ role: 'user', content: messageText, contextId: conversationContextId });
    saveConversationToStorage();

    messageInput.value = '';
    messageInput.style.height = 'auto';
    messageInput.focus();
    sendButton.disabled = true;
    if (isEmojiPickerOpen) toggleEmojiPicker(false);

    if (!apiConfig.apiKey || !apiConfig.apiUrl) {
        displayMessage(AI_ASSISTANT_NAME, 'é”™è¯¯ï¼šAPI å¯†é’¥æˆ–åœ°å€æœªé…ç½®ã€‚è¯·åœ¨è®¾ç½®ä¸­é…ç½®ã€‚', false, AI_AVATAR_CHAR);
        return;
    }
    if (!apiConfig.defaultModel) {
        displayMessage(AI_ASSISTANT_NAME, 'é”™è¯¯ï¼šæœªé€‰æ‹©é»˜è®¤æ¨¡åž‹ã€‚è¯·åœ¨è®¾ç½®ä¸­é€‰æ‹©ã€‚', false, AI_AVATAR_CHAR);
        return;
    }

    const aiMessageId = `msg-ai-${Date.now()}`;
    displayMessage(AI_ASSISTANT_NAME, '', false, AI_AVATAR_CHAR, null, aiMessageId);
    showTypingIndicator();
    stopGeneratingButton.style.display = 'inline-flex';
    abortController = new AbortController();

    callChatAPI(apiConfig.defaultModel, messagesToSend, apiConfig.streaming, aiMessageId, (chunk, done, reasoningChunk) => {
        if (done) {
            hideTypingIndicator();
            stopGeneratingButton.style.display = 'none';
            abortController = null;
            const finalElement = document.getElementById(aiMessageId);
            if (finalElement) {
                const finalContent = finalElement.querySelector('.message-text')?.innerText || '[ç©ºå“åº”]';
                const lastAiMsg = currentConversationHistory.filter(m => m.role === 'assistant').pop();
                if (!lastAiMsg || lastAiMsg.content !== finalContent) {
                    currentConversationHistory.push({ role: 'assistant', content: finalContent, contextId: conversationContextId });
                    saveConversationToStorage();
                }
                while (currentConversationHistory.length > MAX_MESSAGES_DISPLAY * 2) currentConversationHistory.shift();
            }
        }
    });
}

async function callChatAPI(model, messages, streaming, messageId, callback) {
    let url = apiConfig.apiUrl.replace(/\/$/, '');
    const headers = { 'Content-Type': 'application/json' };
    
    // ==========================================================
    // THIS IS THE FIX. Add the Authorization header for non-Gemini providers.
    // ==========================================================
    if (apiConfig.provider !== 'gemini') {
        headers['Authorization'] = `Bearer ${apiConfig.apiKey}`;
    }

    let requestBodyPayload = {};
    let effectiveModelName = model;

    if (apiConfig.provider === 'gemini') {
        // Gemini uses the API key in the URL parameter.
        url += `/${effectiveModelName}:streamGenerateContent?key=${apiConfig.apiKey}&alt=sse`;
        requestBodyPayload = { contents: messages };
    } else {
        // Other providers use the Authorization header.
        url += '/chat/completions';
        effectiveModelName = (apiConfig.provider === 'deepseek' && apiConfig.reasoning) ? 'deepseek-reasoner' : model;
        requestBodyPayload = {
            model: effectiveModelName,
            messages: messages,
            stream: streaming
        };
    }

    if (apiConfig.provider === 'custom' && Array.isArray(apiConfig.customBodyParams)) {
        apiConfig.customBodyParams.forEach(param => {
            try { requestBodyPayload[param.key] = JSON.parse(param.value); }
            catch (e) { requestBodyPayload[param.key] = param.value; }
        });
    }
    const body = JSON.stringify(requestBodyPayload);

    try {
        const response = await fetch(url, { method: 'POST', headers, body, signal: abortController?.signal });
        if (!response.ok) {
            let errorData;
            try { errorData = await response.json(); } catch (e) { errorData = { error: { message: response.statusText } }; }
            // Use the JSON message if available, otherwise fall back to status text.
            const errorDetail = errorData.error?.message || errorData.message || response.statusText;
            const errorMessage = `API Error (${response.status}): ${errorDetail}`;
            console.error(errorMessage, errorData);
            displayMessage(AI_ASSISTANT_NAME, `æŠ±æ­‰ï¼Œè¯·æ±‚å‡ºé”™ï¼š${errorMessage}`, false, AI_AVATAR_CHAR, null, messageId);
            callback(null, true, null);
            return;
        }

        if ((streaming || apiConfig.provider === 'gemini') && response.body) {
            const reader = response.body.getReader();
            const decoder = new TextDecoder();
            let buffer = '';
            let fullResponseContent = '';
            let fullReasoningContent = '';

            while (true) {
                const { done, value } = await reader.read();
                if (done) break;
                buffer += decoder.decode(value, { stream: true });

                let endOfLineIndex;
                while ((endOfLineIndex = buffer.indexOf('\n')) >= 0) {
                    const line = buffer.substring(0, endOfLineIndex).trim();
                    buffer = buffer.substring(endOfLineIndex + 1);

                    if (line.startsWith('data:')) {
                        const dataString = line.substring(5).trim();
                        if (dataString === '[DONE]' && apiConfig.provider !== 'gemini') {
                            reader.cancel();
                            callback(fullResponseContent, true, fullReasoningContent);
                            return;
                        }
                        try {
                            const data = JSON.parse(dataString);
                            let chunkContent = '';
                            let currentReasoningChunk = null;

                            if (apiConfig.provider === 'gemini') {
                                if (data.candidates && data.candidates[0].content && data.candidates[0].content.parts && data.candidates[0].content.parts[0].text) {
                                    chunkContent = data.candidates[0].content.parts[0].text;
                                }
                            } else {
                                if (data.choices && data.choices[0].delta) {
                                    if (data.choices[0].delta.content) chunkContent = data.choices[0].delta.content;
                                    if (apiConfig.provider === 'deepseek' && apiConfig.reasoning && data.choices[0].delta.reasoning_content) {
                                        currentReasoningChunk = data.choices[0].delta.reasoning_content;
                                    }
                                }
                            }

                            if (chunkContent) fullResponseContent += chunkContent;
                            if (currentReasoningChunk) fullReasoningContent += currentReasoningChunk;

                            displayMessage(AI_ASSISTANT_NAME, fullResponseContent, false, AI_AVATAR_CHAR, null, messageId, apiConfig.provider === 'deepseek' ? fullReasoningContent : null);
                        } catch (e) {
                            console.warn('Failed to parse stream data line:', dataString, e);
                        }
                    }
                }
            }
            callback(fullResponseContent, true, apiConfig.provider === 'deepseek' ? fullReasoningContent : null);
        } else {
            const data = await response.json();
            let responseContent = '';
            let reasoningData = null;
            if (apiConfig.provider === 'gemini') {
                if (data.candidates && data.candidates[0].content && data.candidates[0].content.parts && data.candidates[0].content.parts[0].text) {
                    responseContent = data.candidates[0].content.parts[0].text;
                } else if (data.promptFeedback && data.promptFeedback.blockReason){
                    responseContent = `è¯·æ±‚è¢«é˜»æ­¢: ${data.promptFeedback.blockReason}`;
                    if(data.promptFeedback.blockReasonMessage) responseContent += ` - ${data.promptFeedback.blockReasonMessage}`;
                }
            } else {
                if (data.choices && data.choices[0].message && data.choices[0].message.content) {
                    responseContent = data.choices[0].message.content;
                    if (apiConfig.provider === 'deepseek' && apiConfig.reasoning && data.choices[0].message.reasoning_content) {
                        reasoningData = data.choices[0].message.reasoning_content;
                    }
                }
            }
            if (responseContent) {
                 displayMessage(AI_ASSISTANT_NAME, responseContent, false, AI_AVATAR_CHAR, null, messageId, reasoningData);
                 callback(responseContent, true, reasoningData);
             } else {
                 displayMessage(AI_ASSISTANT_NAME, 'é”™è¯¯ï¼šAPIå“åº”æ— æ•ˆæˆ–å†…å®¹ä¸ºç©ºã€‚', false, AI_AVATAR_CHAR, null, messageId);
                 callback(null, true, null);
             }
        }
    } catch (error) {
        if (error.name === 'AbortError') {
            const finalElement = document.getElementById(messageId);
            const partialContent = finalElement?.querySelector('.message-text')?.innerText || '';
            displayMessage(AI_ASSISTANT_NAME, partialContent + '\n[AI å›žå¤å·²æ‰‹åŠ¨åœæ­¢]', false, AI_AVATAR_CHAR, null, messageId);
            if (partialContent.trim()) {
                currentConversationHistory.push({ role: 'assistant', content: partialContent + '\n[æ‰‹åŠ¨åœæ­¢]', contextId: conversationContextId });
                saveConversationToStorage();
            }
        } else {
            console.error('API Call Failed:', error);
            displayMessage(AI_ASSISTANT_NAME, `è¿žæŽ¥ API æ—¶å‡ºé”™ï¼š${error.message}`, false, AI_AVATAR_CHAR, null, messageId);
        }
        callback(null, true, null);
    } finally {
        if (abortController && !abortController.signal.aborted) {
            hideTypingIndicator(); stopGeneratingButton.style.display = 'none'; abortController = null;
        } else if (!abortController) {
            hideTypingIndicator(); stopGeneratingButton.style.display = 'none';
        }
    }
}


async function fetchModels() {
    if (!apiConfig.apiKey || !apiConfig.apiUrl) {
        showSettingsStatus('è¯·å…ˆå¡«å†™ API å¯†é’¥å’Œåœ°å€ã€‚', 'error'); return;
    }
    let modelsUrl = apiConfig.apiUrl.replace(/\/$/, '');
    let fetchHeaders = { 'Content-Type': 'application/json' };

    if (apiConfig.provider === 'gemini') {
        modelsUrl += `?key=${apiConfig.apiKey}`;
    } else {
        modelsUrl += '/models';
        fetchHeaders['Authorization'] = `Bearer ${apiConfig.apiKey}`;
    }

    fetchModelsButton.disabled = true;
    fetchModelsButton.innerHTML = '<i class="fas fa-spinner fa-spin mr-1"></i> èŽ·å–ä¸­...';
    try {
        const response = await fetch(modelsUrl, { method: 'GET', headers: fetchHeaders });
        if (!response.ok) {
            const errorData = await response.json().catch(() => ({ error: { message: response.statusText } }));
            const errorDetail = errorData.error?.message || errorData.message || response.statusText;
            throw new Error(`(${response.status}): ${errorDetail}`);
        }
        const data = await response.json();
        let modelIds = [];
        if (apiConfig.provider === 'gemini') {
            if (data.models && Array.isArray(data.models)) {
                modelIds = data.models
                    .filter(model => model.supportedGenerationMethods?.includes("generateContent") && model.name && !model.name.includes("embedContent") && !model.name.includes("aqa"))
                    .map(model => model.name.startsWith("models/") ? model.name.substring(7) : model.name)
                    .sort((a,b) => {
                        if (a.includes('flash') && !b.includes('flash')) return -1;
                        if (!a.includes('flash') && b.includes('flash')) return 1;
                        if (a.includes('pro') && !b.includes('pro')) return -1;
                        if (!a.includes('pro') && b.includes('pro')) return 1;
                        return a.localeCompare(b);
                    });
            }
        } else {
            if (data.data && Array.isArray(data.data)) modelIds = data.data.map(model => model.id).filter(Boolean).sort();
            else if (Array.isArray(data)) modelIds = data.map(model => typeof model === 'string' ? model : model.id).filter(Boolean).sort();
        }

        if (modelIds.length > 0) {
            apiConfig.models = modelIds; settingsModelListInput.value = modelIds.join(',');
            updateModelSelectDropdown();
            if (!apiConfig.defaultModel || !apiConfig.models.includes(apiConfig.defaultModel)) {
                let preferredModel = apiConfig.models[0];
                if (apiConfig.provider === 'gemini') {
                    preferredModel = apiConfig.models.find(m => m.includes('flash-latest')) || apiConfig.models.find(m => m.includes('flash')) || apiConfig.models.find(m => m.includes('pro-latest')) || apiConfig.models.find(m => m.includes('pro')) || apiConfig.models[0];
                } else if (apiConfig.provider === 'openai') {
                    preferredModel = apiConfig.models.find(m => m.includes('gpt-3.5-turbo')) || apiConfig.models.find(m => m.includes('gpt-4')) || apiConfig.models[0];
                } else if (apiConfig.provider === 'deepseek') {
                    preferredModel = apiConfig.models.find(m => m.includes('deepseek-chat')) || apiConfig.models[0];
                }
                apiConfig.defaultModel = preferredModel;
                settingsModelSelect.value = apiConfig.defaultModel;
            }
            showSettingsStatus('æ¨¡åž‹åˆ—è¡¨å·²æˆåŠŸèŽ·å–ï¼', 'success');
        } else showSettingsStatus('æœªèƒ½èŽ·å–åˆ°æ¨¡åž‹åˆ—è¡¨ï¼Œè¯·æ£€æŸ¥APIé…ç½®æˆ–æ‰‹åŠ¨å¡«å†™ã€‚', 'warning');
    } catch (error) {
        console.error('Fetch models error:', error);
        showSettingsStatus(`èŽ·å–æ¨¡åž‹åˆ—è¡¨å‡ºé”™: ${error.message}`, 'error');
    } finally {
        fetchModelsButton.disabled = false;
        fetchModelsButton.innerHTML = '<i class="fas fa-cloud-download-alt"></i> è‡ªåŠ¨èŽ·å–æ¨¡åž‹';
    }
}

// --- Office Interaction Functions ---

function getSelectedContent() {
  const coercionType = Office.context.host === Office.HostType.Excel ? Office.CoercionType.Table : Office.CoercionType.Text;
  
  Office.context.document.getSelectedDataAsync(coercionType, (asyncResult) => {
    if (asyncResult.status === Office.AsyncResultStatus.Failed) {
      if (Office.context.host === Office.HostType.Excel) {
          Office.context.document.getSelectedDataAsync(Office.CoercionType.Text, (textResult) => {
              if (textResult.status === Office.AsyncResultStatus.Succeeded) {
                  processSelectedContent(textResult.value, false);
              } else {
                  showToast(`èŽ·å–é€‰ä¸­å†…å®¹å¤±è´¥: ${textResult.error.message}`, 'error');
              }
          });
      } else {
        showToast(`èŽ·å–é€‰ä¸­å†…å®¹å¤±è´¥: ${asyncResult.error.message}`, 'error');
      }
    } else {
        const isTable = asyncResult.value.headers !== undefined;
        processSelectedContent(asyncResult.value, isTable);
    }
  });
}

function processSelectedContent(content, isTable) {
    let formattedContent = '';
    if (isTable) {
        const headers = content.headers[0];
        const rows = content.rows;
        const headerLine = `| ${headers.join(' | ')} |`;
        const separatorLine = `|${headers.map(() => '---').join('|')}|`;
        const rowLines = rows.map(row => `| ${row.join(' | ')} |`).join('\n');
        formattedContent = `${headerLine}\n${separatorLine}\n${rowLines}`;
    } else {
        formattedContent = String(content);
    }

    if (formattedContent.trim()) {
        const currentInput = messageInput.value;
        messageInput.value = currentInput ? `${currentInput}\n\n---\nå¼•ç”¨å†…å®¹:\n${formattedContent}` : formattedContent;
        messageInput.focus();
        adjustTextareaHeight();
        sendButton.disabled = false;
        showToast('å·²èŽ·å–é€‰ä¸­å†…å®¹', 'success');
    } else {
        showToast('æœªé€‰ä¸­ä»»ä½•å†…å®¹æˆ–å†…å®¹ä¸ºç©º', 'warning');
    }
}


function insertContent(text) {
  Office.context.document.setSelectedDataAsync(text, { coercionType: Office.CoercionType.Text }, (asyncResult) => {
    if (asyncResult.status === Office.AsyncResultStatus.Failed) {
      showToast(`æ’å…¥å¤±è´¥: ${asyncResult.error.message}`, 'error');
    } else {
      showToast('å†…å®¹å·²æ’å…¥', 'success');
    }
  });
}

function insertCodeOrFormula(codeElement) {
    const rawCode = codeElement.innerText;
    if (!rawCode) return;
    insertContent(rawCode);
}

function addOfficeActionButtons(container) {
    container.querySelectorAll('pre[data-lang]').forEach(pre => {
        if (pre.querySelector('.insert-code-btn')) return;

        const lang = pre.getAttribute('data-lang');
        const codeElement = pre.querySelector('code');
        if (!codeElement) return;

        const actionButton = document.createElement('button');
        actionButton.className = 'message-action-btn insert-code-btn';
        actionButton.style.position = 'absolute';
        actionButton.style.top = '8px';
        actionButton.style.right = '8px';
        actionButton.style.zIndex = '1';

        if (lang.toLowerCase() === 'formula' && Office.context.host === Office.HostType.Excel) {
            actionButton.innerHTML = '<i class="fas fa-calculator mr-1"></i> æ’å…¥å…¬å¼';
        } else {
            actionButton.innerHTML = '<i class="fas fa-code mr-1"></i> æ’å…¥ä»£ç ';
        }

        actionButton.onclick = (e) => {
            e.stopPropagation();
            insertCodeOrFormula(codeElement);
        };
        
        pre.appendChild(actionButton);
        const prismToolbar = pre.querySelector('.toolbar');
        if (prismToolbar) prismToolbar.style.display = 'none';
    });
}


